<template>
  <div>
    <h1 style="width: 500px; margin: auto; text-align: center"></h1>
    <el-form
      :model="loginData"
      label-width="100px"
      class="demo-ruleForm"
      :rules="rules"
      ref="ruleForm"
      style="
        width: 400px;
        margin: 20px auto;
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
        padding: 30px 50px 30px 0px;
      "
    >
      <el-form-item label="" prop="username">
        <el-input type="text" v-model="loginData.username"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input
          type="password"
          v-model="loginData.password"
          show-password
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm"></el-button>
        <el-button @click="reset"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  name: "login",
  data() {
    return {
      loginData: {
        username: "",
        password: "",
      },
      rules: {
        username: [
          { required: true, message: "", trigger: "change" },
          {
            min: 3,
            max: 10,
            message: " 3  10 ",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "", trigger: "change" },
          {
            min: 4,
            max: 10,
            message: " 4  10 ",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    submitForm() {
      this.$refs.ruleForm.validate((valid) => {
        console.log("valid", valid);
        if (valid) {
          const { username, password } = this.loginData;
          this.ajaxFrom('http://112.74.35.224:8841/root',{ username, password },'post').then((res)=>{
             let result = JSON.parse(res).data[0];
            //  console.log(result);
             this.$store.commit('manager/login',result)
             if(this.$store.getters['manager/islogin'])
             {
                this.$router.push('/home');
             }
             else{
               this.$message.error('~');
               this.reset();
             }
          },(err)=>{
            console.log(err);
          })
        }
      });

    },
    goto(path) {
      this.$router.push(path);
    },
    reset() {
      this.loginData.username = "";
      this.loginData.password = "";
    },
    ajaxFrom(url,data,type) {
      return new Promise((resolve, reject) => {
        let xhr = new XMLHttpRequest();
        xhr.open(type, url);
        xhr.setRequestHeader(
          "Content-Type",
          "application/x-www-form-urlencoded"
        );
        xhr.onreadystatechange = function () {
          if (xhr.readyState !== 4) {
            return;
          }
          if (xhr.status === 200) {
            resolve(xhr.responseText);
          } else {
            reject(xhr.responseText);
          }
        };
        xhr.send(JSON.stringify(data));
      });
    },
  },
  created() {},
};
</script>
